  color: #f2f5f7;
}

.intro p {
  margin-top: 0;
  margin-bottom: 40px;
  display: block;
  max-width: 685px;
  font-family: "PT Mono", Courier, monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: #F2F5F7;
  text-align: left;
}

.intro__button {
  box-sizing: border-box;
  display: block;
  margin-bottom: 280px;
  padding: 15px 25px;
  min-width: 410px;
  min-height: 55px;
  background: #00B2FF;
