.advantages ul {
  margin: 0 auto;
  margin-bottom: 120px;
  margin-top: 120px;
  padding: 0;
  max-width: 1222px;
  min-height: 630px;
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(2, 595px);
  grid-template-rows: repeat(2, 300px);
  list-style-type: none;
}

.advantages li {
  position: relative;
  box-sizing: border-box;
  width: 595px;
  padding: 40px 45px 35px;
}
.advantages li::before {
  content: "";
  position: absolute;
  width: 5px;
  height: 300px;
  background-color: #00b2ff;
  left: 0;
  top: 0;
}
.advantages li:hover {
  background-color: #00b2ff;
  color: #f2f5f6;
}

.advantages__item:hover path {
  fill: #f2f5f7;
}
.advantages__item:hover h3 {
  color: #f2f5f7;
}
.advantages__item:hover p {
  color: #f2f5f7;
}

