  font-family: "Roboto Condensed", Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;
}

.questions p {
  margin: 0;
  margin-bottom: 35px;
  font-family: "PT Mono", Courier, monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}

.form {
  margin-bottom: 150px;
  margin-top: 170px;
  margin-right: auto;
  display: flex;
  flex-direction: column;
}

.form__field {
  font-family: "PT Mono", Courier, monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
}

.form__contacts {
  margin-bottom: 20px;
}

.form__contacts label:not(:last-child) {
  margin-right: 30px;
}

.form__contacts input {
  padding: 15px 0 15px 15px;
  width: 275px;
  height: 50px;
  background: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  color: #ffffff;
}
.form__contacts input::placeholder {
  color: #ffffff;
}

.form textarea {
  width: 584px;
  margin-bottom: 35px;
  padding: 0;
  padding-top: 25px;
  padding-left: 15px;
  background: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  color: #ffffff;
}
.form textarea::placeholder {
  color: #ffffff;
}

.form__confirm {
  display: flex;
}

.form__confirm button {
  box-sizing: border-box;
  height: 45px;
  padding: 15px 25px;
  margin-right: 50px;
  background: #041427;
  border: none;
  border-radius: 3px;
  font-family: "Roboto Condensed", Arial, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
}

.form__confirm label {
  margin-top: 15px;
  display: flex;
  color: #ffffff;
}

.form__text {
  line-height: 16px;
  max-width: 234px;
}

input[type=checkbox] + .control-mark {
  display: block;
  margin-right: 11px;
  margin-top: 8px;
  width: 15px;
  height: 15px;
  border: 1px solid #FFFFFF;
  border-radius: 2px;
}

input[type=checkbox]:checked + .control-mark {
  position: relative;
}
input[type=checkbox]:checked + .control-mark::after {
  content: "";
  position: absolute;
  width: 8px;
  height: 5px;
  left: 2px;
  top: 3px;
  background: url("../img/sprite/checkbox-active.svg") no-repeat;
}

.footer-nav {
  position: sticky;
  display: flex;
  margin: 0 auto;
  background: #041827;
}

.footer-nav__section {
  margin-top: 50px;
  margin-bottom: 45px;
}
.footer-nav__section:first-child {
  margin-left: auto;
}
.footer-nav__section:last-child {
  margin-right: auto;
}

.footer-nav__section ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.footer-nav__section li {
  margin-right: 30px;
  margin-bottom: 10px;
}

.footer-nav__button {
  display: none;
}

.footer-nav__section a {
  font-family: "PT Mono", Courier, monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}

.footer-nav__section p {
  margin-top: 5px;
  font-family: "Roboto Condensed", Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  align-items: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #FFFFFF;
}

.footer-social {
  display: flex;
  flex-direction: column;
  margin-right: 160px;
}

.footer-social__logo {
  width: 215px;
  margin-left: auto;
  margin-bottom: 30px;
  display: flex;
}

.footer-social ul {
  display: flex;
  flex-direction: row;
}

.footer-social li:not(:last-child) {
  margin-right: 15px;
}

.footer-social__item a {
  display: flex;
  width: 40px;
  height: 40px;
  align-items: center;
}

.footer-social svg {
  width: 40px;
  height: 40px;
  padding: 5px;
}

.site-sections ul {
  width: 500px;
}

.site-sections li {
  display: block;
  width: 220px;
  height: auto;
}

.site-sections a {
  display: block;
  width: 220px;
  height: auto;
}

.contacts ul {
  flex-direction: column;
}

.contacts {
  min-width: 180px;
}

