.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include vp-767 {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 500px;
    padding: 40px;

    background-color: #00b3ff;
    border-radius: 3px;

    @include vp-767 {
      width: 100%;
      padding: 20px;
    }
  }

  &__header {
    margin: 0;
    margin-bottom: 15px;
    display: block;
    width: 420px;
    font-family: "Roboto Condensed", Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 42px;
    color: #ffffff;
  }

  &__close-btn {
    position: absolute;
    top: 22px;
    right: 22px;
    z-index: 1;

    width: 40px;
    height: 40px;
    padding: 0;

    background: $color-transparent;
    border: none;
    cursor: pointer;
  }

  &__description {
    margin: 0;
    margin-bottom: 40px;
    display: block;
    width: 380px;
    font-family: "PT Mono", Courier, monospace;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #ffffff;
  }

  &__form {
    display: flex;
    flex-direction: column;
  }

  &__input[type="text"] {
    width: 420px;
    margin-bottom: 20px;
    padding: 15px 0 15px 15px;
    background: rgba(4, 20, 39, 0.15);
    border: none;
    border-radius: 3px;
    font-family: "PT Mono", Courier, monospace;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;

    &::placeholder {
      color: #ffffff;
    }
  }

  & textarea {
    width: 420px;
    margin-bottom: 25px;
    padding-top: 25px;
    padding-left: 15px;
    background: rgba(4, 20, 39, 0.15);
    border: none;
    border-radius: 3px;
    font-family: "PT Mono", Courier, monospace;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;

    &::placeholder {
      color: #ffffff;
    }
  }

  &__submit-button {
    width: 420px;
    margin-bottom: 9px;
    padding: 15px 60px;
    background: #041427;
    border: none;
    border-radius: 3px;
    font-family: "Roboto Condensed", Arial, sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__checkbox {
    display: flex;
    align-items: center;
  }

  &__text {
    margin-top: 11px;
    font-family: "PT Mono", Courier, monospace;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    color: #FFFFFF;
    opacity: 0.8;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-767 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
}
